import { Show, createSignal, For } from "solid-js";
import InputWithCopy from "~/components/InputWithCopy";

export default function TaskList() {
  const [content, setContent] = createSignal("");
  const [match, setMatch] = createSignal(`(TE-\\d{4})`);
  const [highlightedLine, setHighlightedLine] = createSignal();
  const [regexError, setRegexError] = createSignal<string>();

  const result = () => {
    setRegexError(undefined);

    try {
      return content()
        .split("\n")
        .map((line) => {
          const regexp = new RegExp(match(), "g");
          const array = [...line.matchAll(regexp)];

          return array.map((match) => match?.[1]);
        });
    } catch (error) {
      setRegexError((error as Error).message);
      return [];
    }
  };

  const details = () => {
    const existing = new Map<string, number>();

    return result().map((result, i) => {
      const details = [
        result.length > 1 ? `(${result.length})` : null,
        result.length === 0 ? `⚠️` : null,
        result.map((task) => existing.has(task)).filter((v) => v).length > 0
          ? {
              value: `👥`,
              class: "cursor-pointer",
              onMouseOver: () =>
                setHighlightedLine(
                  existing.get(result.find((task) => existing.has(task))!)! + 1
                ),
              onMouseOut: () => setHighlightedLine(undefined),
            }
          : null,
      ];

      result.forEach((task) => !existing.has(task) && existing.set(task, i));

      return details.filter((v) => v);
    });
  };

  const unique = (tasks: string[]) => Array.from(new Set(tasks));

  return (
    <main class="text-center mx-auto text-gray-700 p-4">
      <div classList={{ "text-red-600": !!regexError() }}>
        <input
          type="text"
          class="text-center font-sans text-4xl focus:outline-none p-5 leading-none	"
          value={match()}
          onChange={(event) => setMatch(event.target.value)}
        />
        <Show when={regexError()}>
          <p class="mb-8">{regexError()}</p>
        </Show>
      </div>
      <div class="outline-1 flex relative">
        <pre class="w-10 flex-[0_0_2.5rem] py-5 bg-gray-300">
          <For each={details()}>
            {(line) => (
              <>
                <For each={line}>
                  {(content) => {
                    if (typeof content === "string") {
                      return `${content}`;
                    }

                    const { value, ...rest } = content!;

                    return <span {...rest}>{value}</span>;
                  }}
                </For>
                {`\n`}
              </>
            )}
          </For>
        </pre>

        <textarea
          class="w-full border resize-none font-sans p-5 focus:outline-none overflow-y-clip whitespace-nowrap "
          rows={content().split("\n").length}
          onInput={(event) => setContent(event.target.value)}
        ></textarea>
        <Show when={highlightedLine()}>
          <div class={`w-full absolute pointer-events-none pt-5`}>
            <For each={Array((highlightedLine() as number) - 1)}>
              {() => <br />}
            </For>
            <div class={`min-h-[1.5em] bg-yellow-500 bg-opacity-50`}>{``}</div>
          </div>
        </Show>
      </div>
      <Show when={result().flat().length > 0}>
        <div class="my-10">
          <fieldset>
            <legend>List</legend>

            <InputWithCopy
              readOnly={true}
              value={unique(result().flat()).join(" ")}
            />
          </fieldset>

          <fieldset>
            <legend>JQL</legend>
            <textarea
              readOnly={true}
              class="w-full border resize-none font-sans p-5 pl-10 focus:outline-none"
              onInput={(event) => setContent(event.target.value)}
            >
              {`project = "TE" AND key in (${unique(result().flat()).join(
                ", "
              )}) ORDER BY created DESC`}
            </textarea>
          </fieldset>
        </div>
      </Show>
    </main>
  );
}
